@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Oleo+Script|Signika|Nunito:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navbar-container {
  padding: 2rem 2rem;
  padding-bottom: 0rem;
  border: 1rem solid #65AC92;
  border-bottom: none;
}
.Navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  vertical-align: middle;
  padding: 0rem 0rem;
  max-width: 1000px;
  margin: auto;
}
.Navbar-link-container {
  margin-top: auto;
  color: #65AC92;
}
.Navbar-link {
  line-height: 0rem;
  font-family: 'Nunito', sans-serif;
  color: #65AC92;
  margin-right: 0rem;
  margin-left: 1rem;
  font-size: 1.45rem;
  font-weight: 300;
}
.Navbar-link a {
  color: #65AC92;
}

.Navbar-profile {
  /* box-shadow: 0px 25px 20px -9px rgba(0,0,0,0.1); */
  overflow: hidden;
  border-radius: 10px;
  width: 75px;
  height: 75px;
  margin-right: auto;
  flex-shrink: 0;
}
.Navbar-profile img {
  max-height: 90px;
  -webkit-filter: hue-rotate(190deg) sepia(.7);
          filter: hue-rotate(190deg) sepia(.7);
}

.Title h1 {
  font-size: 5rem;
}
.Title-border {
  background-color: #65AC92;
}
.Title-container {
  border-left: 1rem solid #65AC92;
  border-right: 1rem solid #65AC92;
  padding: 2rem;
}
.Title {
  max-width: 1000px;
  height: 100%;
  min-height: 600px;
  margin: auto;
  border: 1px solid #65AC92;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Title-name {
  text-align: center;
  margin: 4rem;
  color: #65AC92;
}

.Title-blurb {
  color: #555555;
  font-family: 'Nunito', sans-serif;
  font-size: 1.5rem;
  line-height: 2rem;
  margin: 0 20%;
  text-align: center;
}

@media only screen and (max-width: 650px) {
  .Title h1 {
    font-size: 3rem;
  }
  .Title h2 {
    font-size: 1.5rem;
  }
  .Title-blurb {
    font-size: 1.25rem;
    line-height: 2rem;
  }
  .Title {
    padding-bottom: 5rem;
    min-height: auto;
  }
}

@media only screen and (max-width: 450px) {
  .Title {
    padding: 1rem;
  }
  .Title-name {
    margin: 1rem;
    font-size: 2rem;
  }
  .Title-blurb {
    font-size: 1.1rem;
    margin: 0 .5rem;
  }
}

.Bubble h2 {
  font-weight: 400;
  font-size: 1.5rem;
}

.Bubble p {
    font-size: 18px;
    padding-top: 1rem;
    line-height: 2.2rem;
    color: #555555;
}
.Bubble-container.left, .bubble-container.right {
  margin: auto;
  padding: 0 2rem;
}
.Bubble-container.left {
  background-color: #65AC92;
  padding-top: 2rem;
}
.Bubble-container.left.bottom {
  background-color: #65AC92;
  padding-top: 0;
  padding-bottom: 2rem;
}
.Bubble-container.left p,
.Bubble-container.left h1,
.Bubble-container.left h2 {
  color: white;
}
.Bubble-container.right {
  background-color: white;
}


.Bubble {
  max-width: 1000px;
  margin: auto;
  border: 1px solid #65AC92;
  padding: 1rem;
  padding-top: 4rem;
  padding-bottom: 4rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
}

.Bubble.right {
  border: none;
  border-right: 1px solid #65AC92;
  border-left: 1px solid #65AC92;
}
.Bubble.left {
  border: none;
  border-right: 1px solid white;
  border-left: 1px solid white;
}
.Bubble.left.top {
  border: none;
  border-right: 1px solid white;
  border-left: 1px solid white;
  border-top: 1px solid white;
}
.Bubble.left.bottom {
  border: none;
  border: 1px solid white;
  border-top: none;
}

.Bubble-image {
  box-shadow: 0px 25px 20px -9px rgba(0,0,0,0.1);
  overflow: hidden;
  border-radius: 5px;
  width: 150px;
  height: 150px;
  margin-right: 5rem;
  margin-left: 10rem;
  flex-shrink: 0;
}
.Bubble-image.right {
  margin-right: 5rem;
  margin-left: 5rem;
}

.Bubble img {
  max-height: 200px;
}
.Bubble-info {
  max-width: 500px;
  flex-grow: auto;
}
.Bubble-info.right {
  /* margin-left: 10rem; */
}

@media only screen and (max-width: 925px) {
  .Bubble-container {
    padding: 3rem 2rem;
  }

  .Bubble-image {
      margin-left: 0rem;
      margin-right: 3rem;
  }
  .Bubble-image.right {
      margin-left: 3rem;
      margin-right: 0rem;
      margin-bottom: 0;
  }
  .Bubble-info.right {
      margin-left: 0rem;
      margin-right: 0rem;
  }
}
@media only screen and (max-width: 600px) {
  .Bubble-container {
    margin: auto;
    padding: 2rem;
    /* border: 1px solid blue; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
  }

  .Bubble-image {
    margin-right: 0;
    margin-bottom: 2rem;
    box-shadow: none;
  }

  .Bubble-image.right {
    display: none;
    margin-right: 0;
    margin-bottom: 2rem;
    box-shadow: none;
  }
  .Bubble-info {
    text-align: center;
  }

}

.Contact-container {
  border-left: 1rem solid #65AC92;
  border-right: 1rem solid #65AC92;
  padding-bottom: 3rem;
}
.Contact {
  margin: auto;
  text-align: center;
  border-bottom: 1px solid #65AC92;
  max-width: 800px;
  padding: 3rem 5rem;
}
.Contact p {
  font-size: 18px;
  padding-top: 1rem;
  line-height: 2.2rem;
  color: #555555;
}
@media only screen and (max-width: 925px) {
  .Contact {
    border-top: none;
    border-bottom: none;
  }
}

.FooterComponent-container {
  display: flex;
  border: 1rem solid #65AC92;
  border-top: none;
}
.FooterComponent {
  margin: auto;
  list-style: none;
  text-align: center;
  color: #555555;
  font-size: .85rem;
  padding-bottom: 2rem;
}

